import { __ } from "../utils";

function applyDeactivateYearMonthOption() {
  var el = document.getElementById("uploads_use_yearmonth_folders");
  el && (el.disabled = true) && el.after(function () {
    var redText = document.createElement("span");
    redText.innerHTML = __("This option is not available because you are using <strong>Real Physical Media Plugin</strong> to physically restructure your files.<br/>");
    return redText;
  }());
}

export { applyDeactivateYearMonthOption };